.wrap {
  overflow: hidden;

  width: 100%;
  min-height: 100%;
}

.non-responsive {
  min-width: 1000px;
  max-width: 100%;
}

.responsive {
  min-width: 320px;
  max-width: 100%;
}

.clearfix {
  &:before,
  &:after {
    display: table; // 2

    content: ' '; // 1
  }
  &:after {
    clear: both; //test
  }
}

.centering-block {
  font-size: 0;

  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  text-align: center;

  &:before {
    display: inline-block;

    width: 0;
    height: 100%;
    margin-right: 0;

    content: '';
    vertical-align: middle;
  }
}

.centering-element {
  font: normal normal normal @font-size/@line-height @font-face, sans-serif;

  position: relative;

  display: inline-block;

  text-align: left;
  vertical-align: middle;
}

.circle {
  border-radius: 100%;
}

.pos-rel {
  position: relative;
}

.pos-abs {
  position: absolute;
}

.abs-center {
  position: absolute;
  top: 0;
