body{
  color: #fff;
  background: #2D2D2D;
}

.example-portfolio{

}

.example-portfolio__contentList{

}

.example-portfolio__contentClose{
  font-size: 64px;
  line-height: 32px;
  color: #fff !important;
  border: 0 !important;
  position: absolute;
  top: 10px;
  right: 60px;
}

.example-portfolio__content{

}

.example-portfolio__contentContainer{
  width: 80%;
  padding: 60px;
  margin: auto;

  img{
    display: block;
    width: 100%;
    margin: 0 0 60px 0;
  }

  h1{
    margin: 0 0 15px 0;
  }
}

.example-portfolio__list{

}

.example-portfolio__item{
  &:hover{
    background-size: 150% 150%;

    .example-portfolio__itemWraper{
      opacity: 1;
    }

    .example-portfolio__itemImage{
      transform: scale(1.2);
    }

    .example-portfolio__itemWraperTitle{
      animation-name: animation-fly-bounce;
    }

    .example-portfolio__itemWraperDescription{
      animation-name: animation-fly-bounce;
      animation-delay: 100ms;
    }
  }
}

.example-portfolio__itemImage{
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: cover;

  transition: transform 4000ms linear;
  transform: scale(1);
}

.example-portfolio__itemWraper{
  position: absolute;
  top: -1%;
  left: -1%;
  display: table;
  width: 102%;
  height: 102%;
  background: rgba(0, 0, 0, 0.60);

  opacity: 0;
  transition: opacity 1000ms ease;
}

.example-portfolio__itemWraperContainer{
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  color: #ffffff;
}

.example-portfolio__itemWraperTitle{
  font-size: 24px;
  padding: 0 10px;
  margin: 0 0 24px 0;

  animation-duration: 1000ms;
  animation-timing-function: linear;
  animation-fill-mode: both;
  animation-iteration-count: 1;
}

.example-portfolio__itemWraperDescription{
  font-size: 18px;
  line-height: 18px;
  padding: 0 10px;

  animation-duration: 1000ms;
  animation-timing-function: linear;
  animation-fill-mode: both;
  animation-iteration-count: 1;
}

.generate-tile-colors(20);

.generate-tile-colors(@n, @i: 1) when (@i =< @n) {
  .example-portfolio__content:nth-child(@{i}) {
    background: hsla((@i+10)*100, 80%, 70%, 0.2);
  }
  .generate-tile-colors(@n, (@i + 1));
}
